.draggable-container {
  /* width: 200px;
  height: 200px; */
  top: "35%";
  left: "30%";
  transform: "translate(-50%, -50%)";
  background-color: #feb127;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 3px;
  padding-left: 2px;
  padding-right: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease-in-out;
  opacity: 1;
  cursor: grab;
  position: fixed;
}

.draggable-container.closed {
  opacity: 0;
}

.draggable-container:not(.closed) {
  cursor: grab;
}

.draggable-container:not(.closed):hover .draggable-button {
  cursor: grab;
}

.draggable-container.closed,
.draggable-container:not(.closed):hover {
  cursor: default;
}

/* SearchBar.css */
.search-bar {
  position: relative;
  display: inline-block;
  width: 500px;
  margin: 1px auto;
}

input[type="text"] {
  margin: auto;
  width: 95%;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 16px;
  border: none;
  outline: none;
  cursor: text;
}

/* input[type="text"]:focus {
  border-color: balck;
  box-shadow: 0 0 5px #000000;
} */

.search-bar-input {
  display: flex;
  align-items: center;
  padding: 5px 15px 5px 15px;
  border-radius: 5px;
  background-color: #fff;
}

.search-icon {
  color: #5f6368;
  margin-right: 5px;
}

.mic-icon {
  margin-left: 5px;
  padding: 5px;
  cursor: pointer;
  color: #5f6368;
  transition: transform 0.2s ease;
}

.mic-icon:hover {
  color: black;
  transform: translateY(-3px);
}

.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
}
